(function() {
  this.App.service('MnoeTenant', ["MnoeAdminApiSvc", function(MnoeAdminApiSvc) {
    var _self;
    _self = this;
    this.get = function() {
      return MnoeAdminApiSvc.one('tenant').get();
    };
    this.update = function(config) {
      return MnoeAdminApiSvc.one('tenant').patch({
        tenant: {
          frontend_config: config
        }
      });
    };
    this.updateDomain = function(params) {
      return MnoeAdminApiSvc.one('tenant').one('domain').patch({
        tenant: params
      });
    };
    this.addSSLCerts = function(params) {
      return MnoeAdminApiSvc.one('tenant').all('ssl_certificates').post({
        tenant: params
      });
    };
    return this;
  }]);

}).call(this);
