(function() {
  this.App.controller('SettingsController', ["toastr", "CONFIG_JSON_SCHEMA", "MnoeTenant", function(toastr, CONFIG_JSON_SCHEMA, MnoeTenant) {
    'ngInject';
    var loadConfig, vm;
    vm = this;
    vm.settingsModel = {};
    vm.settingsSchema = CONFIG_JSON_SCHEMA;
    vm.settingsForm = [
      {
        type: "tabs",
        tabs: [
          {
            title: "System",
            items: ["system"]
          }, {
            title: "Dashboard",
            items: ["dashboard"]
          }, {
            title: "Admin Panel",
            items: ["admin_panel"]
          }
        ]
      }
    ];
    loadConfig = function() {
      return MnoeTenant.get().then(function(response) {
        return vm.settingsModel = response.data.frontend_config;
      });
    };
    loadConfig();
    vm.cancel = function(form) {
      vm.settingsModel = {};
      form.$setPristine();
      return loadConfig();
    };
    vm.saveSettings = function() {
      vm.isLoading = true;
      return MnoeTenant.update(vm.settingsModel).then(function() {
        return toastr.success('mnoe_admin_panel.dashboard.settings.save.toastr_success');
      }, function() {
        return toastr.error('mnoe_admin_panel.dashboard.settings.save.toastr_error');
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
  }]);

}).call(this);
