(function() {
  this.App.controller('SettingsDomainController', ["toastr", "MnoeTenant", function(toastr, MnoeTenant) {
    'ngInject';
    var vm;
    vm = this;
    vm.tenant = {};
    vm.ssl = {};
    vm.isLoading = true;
    MnoeTenant.get().then(function(response) {
      return vm.tenant = response.data;
    })["finally"](function() {
      return vm.isLoading = false;
    });
    vm.uploadCerts = function() {
      vm.isCertSaving = true;
      return MnoeTenant.addSSLCerts(vm.ssl).then(function() {
        return toastr.success('mnoe_admin_panel.dashboard.settings.ssl.save.toastr_success');
      }, function() {
        return toastr.error('mnoe_admin_panel.dashboard.settings.ssl.save.toastr_error');
      })["finally"](function() {
        return vm.isCertSaving = false;
      });
    };
    vm.updateDomain = function() {
      vm.isDomainSaving = true;
      return MnoeTenant.updateDomain(_.pick(vm.tenant, 'domain')).then(function() {
        return toastr.success('mnoe_admin_panel.dashboard.settings.domain.save.toastr_success');
      }, function() {
        return toastr.error('mnoe_admin_panel.dashboard.settings.domain.save.toastr_error');
      })["finally"](function() {
        return vm.isDomainSaving = false;
      });
    };
  }]);

}).call(this);
